body {
  font-family: "Roboto", sans-serif;
  height: 100%;
  margin: 0;
  padding: 0;
}

input {
  margin: 1rem 0;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
}

.boxColor {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 400px;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
}

.buttonReset {
  width: 100px;
  height: 30px;
  margin-top: 30px;
  cursor: pointer;
}

.displayColorPicker {
  width: 300px;
  height: 300px;
  margin: 30px auto 0;
}
